import React from 'react';
import {
  Paper,
  Typography,
  Box,
  useTheme,
  Avatar,
  Grid
} from '@mui/material';
import WorkIcon from '@mui/icons-material/Work';
import './CareerTimeline.css';

const careerData = [
  {
    company: "Tata Infotech Ltd.",
    duration: "May 2000  Oct 2003",
    designation: "Senior Software Engineer",
    logo: "/images/tata-infotech-logo.png",
    description: "Worked on various software development projects focusing on system architecture and implementation.",
    achievements: [
      "Led development of enterprise applications",
      "Implemented robust system architectures",
      "Mentored junior developers"
    ]
  },
  {
    company: "Cognizant Technologies",
    duration: "Oct 2003  Sep 2019",
    designation: "Delivery Lead",
    logo: "/images/cognizant.svg",
    description: "Led multiple teams in delivering complex software solutions and managing client relationships.",
    achievements: [
      "Managed teams of 20+ developers",
      "Delivered multiple successful projects",
      "Improved client satisfaction scores"
    ]
  },
  {
    company: "CoreCard Software",
    duration: "Nov 2019  Present",
    designation: "Principal Project Analyst",
    logo: "/images/corecard.png",
    description: "Currently leading project analysis and implementation for credit card processing systems.",
    achievements: [
      "Implemented innovative solutions",
      "Reduced processing time by 40%",
      "Improved system reliability"
    ]
  }
];

const CareerTimeline = () => {
  const theme = useTheme();

  return (
    <section id="timeline" className="py-5">
      <div className="container">
        <Typography 
          variant="h4" 
          component="h2" 
          align="center" 
          gutterBottom
          sx={{ 
            mb: 4,
            color: theme.palette.mode === 'dark' ? 'primary.light' : 'primary.main'
          }}
        >
          Career Timeline
        </Typography>
        <div className="timeline">
          {careerData.map((item, index) => (
            <div key={index} className="timeline-event">
              <div className="timeline-event-content">
                <Paper
                  elevation={3}
                  sx={{
                    p: 3,
                    backgroundColor: theme.palette.mode === 'dark' ? 'grey.800' : 'grey.50',
                    transition: 'transform 0.3s ease-in-out',
                    '&:hover': {
                      transform: 'translateY(-5px)',
                    },
                  }}
                >
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <Avatar
                      src={item.logo}
                      alt={`${item.company} logo`}
                      sx={{
                        width: 60,
                        height: 60,
                        mr: 2,
                        bgcolor: theme.palette.primary.main
                      }}
                    >
                      <WorkIcon />
                    </Avatar>
                    <Box>
                      <Typography variant="h6" component="h3">
                        {item.designation}
                      </Typography>
                      <Typography variant="body2" color="text.secondary">
                        {item.duration}
                      </Typography>
                    </Box>
                  </Box>
                  <Typography variant="body1" paragraph>
                    {item.description}
                  </Typography>
                  <Box component="ul" sx={{ pl: 2 }}>
                    {item.achievements.map((achievement, idx) => (
                      <Typography
                        key={idx}
                        component="li"
                        variant="body2"
                        sx={{
                          mb: 1,
                          position: 'relative',
                          '&::before': {
                            content: '""',
                            color: theme.palette.primary.main,
                            fontWeight: 'bold',
                            display: 'inline-block',
                            width: '1em',
                            ml: '-1em'
                          }
                        }}
                      >
                        {achievement}
                      </Typography>
                    ))}
                  </Box>
                </Paper>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default CareerTimeline;
