import React, { useState, useMemo, useEffect } from 'react';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import AOS from 'aos';
import 'aos/dist/aos.css';
import NavigationBar from './components/Navbar';
import Hero from './components/Hero';
import Summary from './components/Summary';
import Skills from './components/Skills';
import Experience from './components/Experience';
import Certifications from './components/Certifications';
import Education from './components/Education';
import Recognition from './components/Recognition';
import Contact from './components/Contact';
import CareerTimeline from './components/CareerTimeline';
import './App.css';

function App() {
  const [darkMode, setDarkMode] = useState(false);

  useEffect(() => {
    AOS.init({
      duration: 1000,
      once: true,
      offset: 100
    });
  }, []);

  const theme = useMemo(
    () =>
      createTheme({
        palette: {
          mode: darkMode ? 'dark' : 'light',
          primary: {
            main: '#1a237e',
          },
          secondary: {
            main: '#ff4081',
          },
          background: {
            default: darkMode ? '#121212' : '#ffffff',
            paper: darkMode ? '#1e1e1e' : '#ffffff',
          },
        },
        components: {
          MuiPaper: {
            styleOverrides: {
              root: {
                backgroundImage: 'none',
              },
            },
          },
        },
      }),
    [darkMode]
  );

  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
    document.body.classList.toggle('dark-mode');
    document.body.classList.toggle('light-mode');
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <div className="App">
        <NavigationBar darkMode={darkMode} toggleDarkMode={toggleDarkMode} />
        <main>
          <section id="home" data-aos="fade-up">
            <Hero />
          </section>
          <section id="summary" data-aos="fade-up">
            <Summary />
          </section>
          <section id="timeline" data-aos="fade-up">
            <CareerTimeline />
          </section>
          <section id="skills" data-aos="fade-up">
            <Skills />
          </section>
          <section id="experience" data-aos="fade-up">
            <Experience />
          </section>
          <section id="certifications" data-aos="fade-up">
            <Certifications />
          </section>
          <section id="education" data-aos="fade-up">
            <Education />
          </section>
          <section id="recognition" data-aos="fade-up">
            <Recognition />
          </section>
        </main>
        <footer id="contact" data-aos="fade-up">
          <Contact />
        </footer>
      </div>
    </ThemeProvider>
  );
}

export default App;
